.btn{
    width: 38px;
    height: 38px;
    
    margin-right: 10px;
    border-radius: 50%;
    border: 1px solid #000000;
}
@media screen and (min-width: 768px){
.btn{
margin-right: 24px;
}    
}
@media screen and (min-width: 1024px){
.btn{

    }
}

.btn:first-child{
    margin-right: 5px;
    
}
.btn:hover{
    background-color: #111111;
    color: #B4AFAF;
}
.btn:focus{
    background-color: #111111;
    color: #B4AFAF;
}

.btn-two{
    width: 38px;
    height: 38px;
    background: none;
    margin-right: 5px;
    border: 1px solid #000000;
    border-radius: 100px;
}
@media screen and (min-width: 768px){
    .btn-two{
        margin-right: 8px;
    }
}
@media screen and (min-width: 1024px){
.btn-two{
    margin-right: 8px;
}    
}

.btn-two :last-child{
margin: 0px;
padding: 0px;
}
.btn-two:hover{
    background-color: #111111;
    color: #B4AFAF;
}
.btn-two:focus{
    background-color: #111111;
    color: #B4AFAF;
}

.none{
display: none;    
}
@media screen and (min-width: 768px){
.none{
display: block;    
}    
}
@media screen and (min-width: 1024px){
